@import '../../scss/_variables';

#sidebar {
    position: fixed;
    top: 8vh;
    left: 0;
    width: 100%;
    height: 20vh;
    z-index: 20;

    h3 {
        cursor: pointer;
    }
}


@media (min-width: $breakpoint-small) {
    #sidebar {
        top: calc(8vh + 20px);
        left: 20px;
        width: 20vw;
        height: 80vh;
    }
}